@import url('https://fonts.googleapis.com/css2?family=Fjord+One&display=swap');

.surat-list-card-warpper{
  display: inline-table;
  width: 22%;
  margin-left: 38px;
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  height: 100px;
  padding: 10px;
  align-items:center;
  margin-top: 20px;
  border-radius: 5px;
  background-color: #FFFF ;
  position:relative;

}


.surat-list-card-surat-wrapper{
  display: flex;
  flex-direction: row;
  width: 100%;
}

.surat-list-nomor-surat-wrapper{
  align-items:center;
  width: 20%;
}

.surat-list-box-number-wrapper{
  height: 45px;
  box-sizing: border-box;
  width: 45px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 5px;
  background-color: #E9E6D7;
}

.surat-list-box-number-wrapper p{
  color: #010506;
  position: absolute;
  padding-left: .3%;
  text-align:center; 
  font-size: 18px;
  font-family: 'Fjord One', serif;

}


.surat-list-nama-surat-latin-wrapper{
  width: 55%;
}


.surat-list-nama-surat-latin-wrapper .surat-list-p-nama{
  color: #0D8292;
  position: absolute;
  padding-left: .3%;
  text-align:center; 
  font-size: 16px;
  font-family: 'Fjord One', serif;
  font-weight: bold;

}

.surat-list-nama-surat-latin-wrapper .surat-list-p-arti{
  padding-top: 15px;
  color: #5a5959;
  position: absolute;
  padding-left: .3%;
  text-align:center; 
  font-weight: bold;
  font-size: 12px;
  font-family: 'Fjord One', serif;

}

.surat-list-nama-surat-arab-wrapper{
  padding: 5px;
  width: 25%;
}

.surat-list-nama-surat-arab-wrapper .surat-list-p-nama-arab{
  color: #0D8292;
  margin-top: -1px;
  position: absolute;
  padding-left: .3%;
  text-align:center; 
  font-size: 20px;
  font-family: 'Fjord One', serif;
  font-weight: bold;

}

.surat-list-nama-surat-arab-wrapper .surat-list-p-ayat{
  padding-top: 1px;
  color: #5a5959;
  position: absolute;
  padding-left: .3%;
  text-align:center; 
  font-weight: bold;
  font-size: 14px;
  font-family: 'Fjord One', serif;

}

.surat-list-card-warpper:hover{
  background-color: #d0dadc;
  -webkit-transform: scale(1.08);
  transform: scale(1.08);
  cursor: pointer;
}


#surat-list-btn-show-more {
  background-color: #FFB91B;
  border: 1px solid #FFB91B;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, .1) 0 2px 4px 0;
  box-sizing: border-box;
  color: #5a5959;
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
  outline: none;
  outline: 0;
  padding: 10px 25px;
  text-align: center;
  margin-left: -46%;
  margin-top: 8%;
  position: absolute;
}

#surat-list-btn-show-more:hover {
  -webkit-transform: scale(1.08);
  transform: scale(1.08);
  cursor: pointer;
}

